html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

.page {
    height: 100%;
    font-family: var(--font-family, sans-serif);
    -webkit-text-size-adjust: 100%;
    scroll-behavior: smooth;
}

.page__body {
    margin: 0;
    padding: 0;
    min-width: 320px;
    min-height: 100%;
    font-size: 16px;
}

picture {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    line-height: 0;
}

img,
source {
    width: 100%;
    height: auto;
    max-width: 100%;
    object-fit: cover;
}

a {
    color: currentColor;
    text-decoration: none;
}

.site-container {
    overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr auto;
}

.is-hidden {
    display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: -0.01em;
}

.btn-reset {
    border: none;
    padding: 0;
    background: transparent;
    cursor: pointer;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.input-reset {
    -webkit-appearance: none;
    appearance: none;
    border: none;
    border-radius: 0;
    background-color: #fff;

    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration {
        display: none;
    }
}

.visually-hidden {
    position: absolute;
    overflow: hidden;
    margin: -1px;
    border: 0;
    padding: 0;
    width: 1px;
    height: 1px;
    clip: rect(0 0 0 0);
}

.container {
    margin: 0 auto;
    padding: 0 var(--container-offset);
    max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.centered {
    text-align: center;
}

.dis-scroll {
    position: fixed;
    left: 0;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    overscroll-behavior: none;
}

.page--ios .dis-scroll {
    position: relative;
}
